import React, { useState, useEffect } from 'react';
import { Carousel } from 'react-bootstrap';

import { HomeImageContainer, ImageContainer } from './home-image.styles';

import image1 from '../../assets/1.jpg';
import image2 from '../../assets/2.jpg';
import image3 from '../../assets/3.jpg';
import image4 from '../../assets/4.jpg';

const HomeImage = () => {
    return (
        <HomeImageContainer>
            <Carousel>
                <Carousel.Item>
                    <img src={image2} alt="First" />
                    <Carousel.Caption>
                        <h3>公告 Notice</h3>
                        <p>營運幻想廳相關網站的支出費用皆由顯示於網站上的廣告負責，廣告收入高低可能影響因素有可能於當月是廣告業投放之淡季或者是使用者們無效點擊廣告等。無法支出主機費用就有可能導致無法將網站繼續營運，現在我們採用意願性方式贊助，歡迎有意願的廳友或使用者贊助我們。</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                        <img src={image4} alt="Second" />
                    <Carousel.Caption>
                        <h3></h3>
                        <p>想知道更多的更新翻譯消息嗎...？</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img src={image3} alt="Second" />
                    <Carousel.Caption>
                        <h3></h3>
                        <p>官方的主程式無法安裝？來試試看幻想廳封裝的主程式。</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img src={image1} alt="Second" />
                    <Carousel.Caption>
                        <h3>糟糕！？</h3>
                        <p>發現網站資料錯誤...</p>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        </HomeImageContainer>
    );
};

export default HomeImage;